import request from "../../utils/request.js"
export default {
	data() {
		return {
			newPopupFlag:false,
			commentData:[],
			pageNum:1,
			pageSize:8,
			pageCount:0
		};
	},
	props: ["type",'pHeight','popupFlag'],
	created(){
		//this.initPopup()
		//this.getCommentInfo()
	},
	mounted() {
		this.initPopup()
		//this.getCommentInfo()
	},
	methods:{
		getCommentInfo(id){
			console.log(id)
			let _this = this
			request({
				url: '/index/getCommentInfo', //服务器端地址
				data: {
					pageNum:1,
					pageSize:8,
					videoId:id
				},
				method: 'GET',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					console.log(res)
					if (res.data.status === 200) {
						_this.commentData = res.data.data.data
						_this.pageCount =  res.data.data.pageCount
						console.log(_this.flag)
					}
				}
			});
		},
		changeShow(id){
			this.getCommentInfo(id)
			this.newPopupFlag = !this.newPopupFlag
		},
		initPopup(){
		
		},
		closePopup(){
			this.newPopupFlag = false
		}
	}
}
